import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import { Header, PanelHeaderButton, platform, IOS, ActionSheetItem, ActionSheet } from '@vkontakte/vkui';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Info from './panels/Info';

const osName = platform();

class App extends React.Component {
	constructor(props) {
		super(props);
	
			this.state = {
			activePanel: 'home',
			setActivePanel: 'home',
            fetchedUser: null,
			popout: null,
			setUser: null,
			popout: null
			};
		this.openBase = this.openBase.bind(this);
	}
	
    openBase() {
    this.setState({ popout:
      <ActionSheet onClose={() => this.setState({ popout: null })}>
        <ActionSheetItem autoclose>
          По дням
        </ActionSheetItem>
        <ActionSheetItem autoclose>
          По неделям
        </ActionSheetItem>
        <ActionSheetItem autoclose>
          По месяцам
        </ActionSheetItem>
        {osName === IOS && <ActionSheetItem autoclose mode="cancel">Отменить</ActionSheetItem>}
      </ActionSheet>
    });
  }

	componentDidMount() {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			this.state.setUser(user);
			this.state.popout(null);
		}
		this.openBase();
		fetchData();
	};
	
	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	
	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id='home' fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id='persik' go={this.go} />
				<Info id="info" openBase={this.state.openBase} fetchedUser={this.state.fetchedUser} go={this.go} />
			</View>
		);
	}
}	

export default App;

